.App {
  text-align: center;
  /* background-color: rgba(48, 84, 126, 0.61); */
  /* background: rgb(0,219,255); */
  background: radial-gradient(circle, rgba(0,219,255,1) 0%, rgba(9,70,121,1) 100%);
}



a {
  color: rgb(178, 229, 241);
}

 .border{
   display: flex;
   flex-direction: column;
   padding: 50px 50px;
   /* background-color: rgba(48, 84, 126, 0.61); */
   align-items: center;
 }
img{
  background-color: rgb(159, 224, 235);
  box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.3), 
  0 8px 30px 0 rgba(0, 0, 0, 0.3);

}
 button{
   font-weight: 600;
   width: 150px;
   border-radius: 10px;
    background-color: rgb(159, 224, 235);
   box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
 }

 /* button shadow smaller on hover */
  button:hover{
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.2), 0 4px 12px 0 rgba(0, 0, 0, 0.19);
  }

 .tip{
  cursor: pointer;
  position: absolute;
  display: inline-block;
  transform: translate(10px, -5px);
   color: bisque;
   background-color: #4e5258ad;
   border-radius: 4px ;
   padding: 0 5px;
 }


.tipper{
  cursor: pointer;
   position: absolute;
   display: inline-block;
  transform: translate(40px, -8px);
  color: bisque;
  background-color: #4e5258ad;
  border-radius: 4px;
  padding: 5px 15px 7px ;
  max-width: 250px;
}
.tipper2{
  transform: translate(55px, -8px);
}

#crackBtn{
  background-color: rgba(255, 0, 0, 0.616);
  color: bisque;
}

#userInput{

  width: 90px;
  height: 30px;
  border-radius: 10px;
  padding: 0 0 0 15px;
  font-size: 1.2rem;
  padding: auto;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: visible !important; 
  /* height: 100px; 
  width: 50px;
  position: absolute;
  left: 250px;
  margin: 0; 
  z-index: 5000; */
  opacity: 1;
}



 @media (max-width:950px){

  .tipper{
    cursor: pointer;
    position: relative;
    display: block;
    transform: inherit;
    color: bisque;
    background-color: #4e5258ad;
    border-radius: 4px;
    padding: 5px 15px 7px ;
    max-width: 250px;
    min-width: 230px;
    margin-top: 5px;
  }
  .tipper2{
    /* transform: translate(-172px, 25px); */
    margin-top: 5px;
    position: relative;
    transform: inherit;
    display: block;
    /* background-color: #4e5258; */
    min-width: 250px;
  }
.tip{
  margin-right: 200px;
}

 }